            return {
                title: 'accordion',
                lists: [
                    {
                        title: "怎么完成任务？",
                        type: 'font',
                        show: false
                    },
					{
						title: "任务没完成怎么办？",
						type: 'font1',
						show: false
					},
					{
						title: "做任务的正确方法？",
						type: 'font2',
						show: false
					},
					{
						title: "任务过期怎么办？",
						type: 'font3',
						show: false
					},
					{
						title: "忘记用户名密码怎么办？",
						type: 'font4',
						show: false
					}
                ]
            }
        },
        methods: {
            trigerCollapse(e) {
                for (let i = 0, len = this.lists.length; i < len; ++i) {
                    if (e === i) {
                        this.lists[i].show = !this.lists[i].show;
                    } else {
                        this.lists[i].show = false;
                    }
                }
            }
        }
    }



